<template>
  <div>
    <div style="width:60%;margin:auto;">

      <form class="needs-validation" novalidate>
        <label></label>
        <b-form-select v-model="selected" :options="options" class="mb-3" />
        <label></label>
        <b-form-input v-model="University" type="text" placeholder="" style="width:30%" ></b-form-input>
        <label></label>
        <b-form-input v-model="name_cn" type="text" placeholder="" style="width:30%" ></b-form-input>
        <b-form-input v-model="name_en" type="text" placeholder="" style="width:30%" ></b-form-input>
        <label></label>
        <b-form-input v-model="email" type="email" placeholder="" style="width:30%" ></b-form-input>
        <label></label>
        <b-form-input v-model="phone" type="text" placeholder="" style="width:30%" ></b-form-input>
        <label></label>
        <b-form-input v-model="password" type="password" placeholder="" style="width:30%" ></b-form-input>
        <b-form-input type="password" placeholder="" style="width:30%" ></b-form-input>
        <label></label>
        <b-form-textarea v-model="address" placeholder="" :rows="3":max-rows="6"> </b-form-textarea>
        <label></label>
        <b-form-input v-model="title_1" type="text" placeholder="" style="width:50%" ></b-form-input>
        <b-form-input v-model="title_2" type="text" placeholder="" style="width:50%" ></b-form-input>
        <b-form-input v-model="title_3" type="text" placeholder="" style="width:50%" ></b-form-input>
        <label></label>
        <b-form-file v-model="file" :state="Boolean(file)" placeholder="Choose a file..."></b-form-file>
          <div class="mt-3">Selected file: {{file && file.name}}</div>
        <label></label>
        <b-form-checkbox  id="checkbox1"
                     v-model="status"
                     value="true"
                     unchecked-value="false">
                     I accept the terms and use
                   </b-form-checkbox>
        <b-button>Submit</b-button>
    </form>
      </div>
      

</div>
</template>
<script>
export default
{
  name:'register',
  data() {return {
    file: null,
    selected: null,
    University:'',
    name_cn:'',
    name_en:'',
    email:'',
    phone:'',
    password:'',
    address:'',
    title_1:'',
    title_2:'',
    title_3:'',
    status:'false',

      options: [
        { value: null, text: '' },
        { value: 'Singapore', text: '' },
        { value: 'Malaysia', text: '' },
        { value: 'China', text: '' },
        { value: 'Hong Kong', text: ''},
        {value:'Macao',text:''},
        {value:'Taiwan',text:''},
        {value:'Australia',text:''}
      ]

  }}
}
</script>
<style scoped>
label{margin-top:20px;}
</style>



